            warning-text="Âà†Èô§Ë¥¶Âè∑Â∞Ü‰ºöÊ∞∏‰πÖÂà†Èô§ÊÇ®ÁöÑÊâÄÊúâÊï∞ÊçÆÔºåÂåÖÊã¨Ôºö"
            :warning-items="[
