                  </div>
                  <div class="info-card">
                    <div class="info-item">
                      <label class="info-label">ÈÇÆÁÆ±</label>
                      <p class="info-content">{{ editForm.email || 'Êú™ËÆæÁΩÆ' }}</p>
                    </div>
                  </div>
                </div>
              </template>
              
              <!-- ÁºñËæëË°®Âçï -->
              <form v-else @submit.prevent="handleUpdateInfo">
                <!-- Áî®Êà∑Âêç -->
                <div class="mb-3">
                  <label class="form-label">Áî®Êà∑Âêç</label>
                  <input
                    type="text"
