                    :disabled="updating"
                  >
                    <span v-if="updating" class="spinner-border spinner-border-sm me-2"></span>
                    {{ updating ? '‰øùÂ≠ò‰∏≠...' : 'üíæ ‰øùÂ≠ò' }}
                  </button>
                  <button
                    type="button"
                    class="btn btn-outline-secondary"
                    @click="cancelBasicEdit"
                    :disabled="updating"
                  >
                    ÂèñÊ∂à
                  </button>
                </div>
              </form>
            </div>
          </div>
          
          <!-- ÊóÖË°åËÄÖËµÑÊñô -->
          <div class="card shadow-sm mb-4">
            <div class="card-header bg-white d-flex justify-content-between align-items-center">
              <h5 class="mb-0">‚ú® ÊóÖË°åËÄÖËµÑÊñô</h5>
              <button
                v-if="!isEditingProfile"
                class="btn btn-sm btn-outline-primary"
                @click="isEditingProfile = true"
              >
                ‚úèÔ∏è ÁºñËæë
              </button>
            </div>
            <div class="card-body">
              <!-- Áî®Êà∑Á≠âÁ∫ß -->
              <div class="mb-3">
                <label class="form-label">üéñÔ∏è ÂΩìÂâçÁ≠âÁ∫ß</label>
                <div>
                  <span :class="'badge level-badge ' + getLevelClass(profileData.level)">
                    {{ getLevelText(profileData.level) }}
                  </span>
                </div>
                <small class="text-muted">Ê†πÊçÆÊóÖË°åÂíåËØÑËÆ∫Êï∞ÈáèËá™Âä®ËÆ°ÁÆó</small>
              </div>
              
              <!-- Âè™ËØªÊòæÁ§∫ -->
              <template v-if="!isEditingProfile">
                <div class="info-display">
                  <div class="info-card mb-3">
                    <label class="info-label">üìù ‰∏™‰∫∫ÁÆÄ‰ªã</label>
                    <p class="info-content">{{ profileData.bio || 'ËøòÊ≤°ÊúâÂÜô‰∏™‰∫∫ÁÆÄ‰ªã' }}</p>
                  </div>
                  <div class="info-card mb-3">
                    <label class="info-label">üè∑Ô∏è ‰∏™‰∫∫Ê†áÁ≠æ</label>
                    <div v-if="profileData.tags">
                      <span 
                        v-for="(tag, index) in profileData.tags.split(',')" 
                        :key="index"
                        class="badge bg-light text-dark me-1"
                      >
                        {{ tag.trim() }}
                      </span>
                    </div>
                    <p v-else class="info-content text-muted">ËøòÊ≤°ÊúâÊ∑ªÂä†Ê†áÁ≠æ</p>
                  </div>
                  <div class="info-card">
                    <label class="info-label">üåç ËÆøÈóÆËøáÁöÑÂõΩÂÆ∂</label>
                    <p class="info-content">{{ profileData.visited_countries || 'ËøòÊ≤°ÊúâËÆ∞ÂΩï' }}</p>
                  </div>
                </div>
              </template>
              
              <!-- ÁºñËæëË°®Âçï -->
              <form v-else @submit.prevent="handleUpdateProfile">
                <!-- ‰∏™‰∫∫ÁÆÄ‰ªã -->
                <div class="mb-3">
                  <label class="form-label">üìù ‰∏™‰∫∫ÁÆÄ‰ªã</label>
                  <textarea
                    class="form-control"
                    v-model="profileData.bio"
                    rows="4"
                    maxlength="500"
                    placeholder="‰ªãÁªç‰∏Ä‰∏ãËá™Â∑±Âêß..."
                  ></textarea>
                  <small class="text-muted">{{ profileData.bio?.length || 0 }}/500</small>
                </div>
                
                <!-- Áî®Êà∑Ê†áÁ≠æ -->
                <div class="mb-3">
                  <label class="form-label">üè∑Ô∏è ‰∏™‰∫∫Ê†áÁ≠æ</label>
                  <input
                    type="text"
                    class="form-control"
                    v-model="profileData.tags"
                    placeholder="‰æãÂ¶ÇÔºöÊëÑÂΩ±Áà±Â•ΩËÄÖ,ÁæéÈ£üËææ‰∫∫,Êà∑Â§ñËøêÂä®"
                    maxlength="200"
                  />
                  <small class="text-muted">ÈÄóÂè∑ÂàÜÈöîÔºåÊúÄÂ§ö10‰∏™Ê†áÁ≠æÔºåÊØè‰∏™‰∏çË∂ÖËøá20Â≠ó</small>
                </div>
                
                <!-- ËÆøÈóÆËøáÁöÑÂõΩÂÆ∂ -->
                <div class="mb-3">
                  <label class="form-label">üåç ËÆøÈóÆËøáÁöÑÂõΩÂÆ∂</label>
                  <input
                    type="text"
                    class="form-control"
                    v-model="profileData.visited_countries"
                    placeholder="‰æãÂ¶ÇÔºö‰∏≠ÂõΩ,Êó•Êú¨,Ê≥∞ÂõΩ"
                    maxlength="200"
                  />
                  <small class="text-muted">ÈÄóÂè∑ÂàÜÈöîÁöÑÂõΩÂÆ∂ÂàóË°®</small>
                </div>
                
                <div class="d-flex gap-2">
                  <button
                    type="submit"
                    class="btn btn-primary"
                    :disabled="updatingProfile"
                  >
                    <span v-if="updatingProfile" class="spinner-border spinner-border-sm me-2"></span>
                    {{ updatingProfile ? '‰øùÂ≠ò‰∏≠...' : 'üíæ ‰øùÂ≠ò' }}
                  </button>
                  <button
                    type="button"
                    class="btn btn-outline-secondary"
                    @click="cancelProfileEdit"
                    :disabled="updatingProfile"
                  >
                    ÂèñÊ∂à
                  </button>
                </div>
              </form>
            </div>
